import { Component } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.css']
})
export class FileUploadComponent {
  apiUrl = 'http://localhost:8080';

  form: FormGroup;
  loading = false;
  imageSrc = '/assets/img/';

  result; // file upload     

  constructor(
    private fb: FormBuilder,
    private http: HttpClient) {
    this.form = this.fb.group({
      file: ['', Validators.required]
    });
  }

  onFileChange(files: FileList) {
    if (files && files.length > 0) {
      // For Preview
      const file = files[0];
      const reader = new FileReader();

      /*         .
           img    .
        FileReader.readAsDataURL     
        base64     , img  . */
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.imageSrc = reader.result;
      };

      /* reactive form input[type="file"]  .
              set 
        https://github.com/angular/angular.io/issues/3466
        form validation  file.name  set. */
      this.file.setValue(file.name);
    }
  }

  onSubmit(files: FileList) {
    const formData = new FormData();
    formData.append('file', files[0]);

    this.loading = true;
    // Send data (payload = formData)
    console.log(formData.get('file'));
  //    .
    this.http.post(`${this.apiUrl}/uploadFile`, formData)
      .subscribe(res => {
        this.result = res;
        this.loading = false;
        this.file.setValue(null);
        alert(" ."+this.result);
      });
  }

  get file() {
    return this.form.get('file');
  }
}
