import { Component, OnInit } from '@angular/core';
import { HttpClientModule,HttpHeaders, HttpClient,HttpErrorResponse } from '@angular/common/http';
import { Order } from "../../shared/models/order";
import { LocalDataSource } from 'ng2-smart-table';
import { Buffer } from "buffer";
import { TokenService } from "../../shared/services/token.service";
@Component({
  selector: 'app-admin-order',
  templateUrl: './admin-order.component.html',
  styleUrls: ['./admin-order.component.css']
})
export class AdminOrderComponent implements OnInit {

  constructor(
    private http:HttpClient
  ) { }

  source: LocalDataSource;
  orderList: Order[];
  settings = {
  mode: 'inline',
  add: {
    confirmCreate: 'true',
    addButtonContent: ''
  },
  actions: {
    add: 'false',
    delete: 'false',
    columnTitle:''
  },
  edit: {
    saveButtonContent: '',
    editButtonContent: '',
    cancelButtonContent: '',
    confirmSave: 'true'
  },
  delete: {
    deleteButtonContent: '',
    confirmDelete: 'true'
  },
  columns: {
    ono: {
      title: '',
      editable: 'false'
    },
    uid: {
      title: '',
      editable: 'false'
    },
    oaddr:{
      title: '',
      editable: 'false'
    },
    ototal: {
      title: '',
      editable: 'false'
    },
    ostatus: {
      title: '',
      editor: {
        type: 'list',
        config: {
          list: [
            { value: 'Y', title: '' },
            { value: 'N', title: '' },
            { value: 'C', title: '' }
          ] },
      }
    },
    odate: {
      title: '',
      editable: 'false'
    }
  }
};

updateRecord(event) {
    var data = {"ono" : event.newData.ono,
                "ostatus" : event.newData.ostatus
                };
  this.http.put<Order>('http://localhost:8080/toma/order/', data).subscribe(
        res => {
          console.log(res);
          event.confirm.resolve(event.newData);
          alert(" ");
          this.ngOnInit();
      },
      (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          alert("Client-side error occured.");
        } else {
          alert("Server-side error occured.");
        }
      });
  }

getOrderList(){
    this.http.get<Order[]>('http://localhost:8080/toma/order/')
    .subscribe((orderList: Order[]) => {
      this.orderList = orderList;
    },(err: HttpErrorResponse) => {
      if (err.error instanceof Error) {
        alert("Client-side error occured.");
      } else {
        alert("Server-side error occured.");
      }
    });
}


  ngOnInit() {
        this.getOrderList();
  }


}
